import React from 'react';
import styles from 'src/styles/Header.module.css';

const config = {
    AvatarURL: 'https://cdn.discordapp.com/avatars/717434146871443456/0a83c747852f4097f0ddba5cc9365760.png'
};

class HeaderInfo extends React.Component {
    click() {
        document.location.href = '/site/';
    };

    render() {
        return (
            <div className={styles.info} onClick={this.click}>
                <div className={styles.iconWrapper}>
                    <img src={config.AvatarURL} className={styles.icon} />
                </div>
                <h1 className={styles.name}>
                    Kanami
                </h1>
            </div>
        );
    };
};

class HeaderContent extends React.Component {
    render() {
        return (
            <div className={styles.content}>
                <nav>
                    {this.props.children}
                </nav>
            </div>
        );
    };
};

class HeaderContentItem extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            active: false
        };
    };

    componentDidMount() {
        let page = this.props.page;

        if (page === document.location.pathname) {
            this.setState({
                active: true
            });
        } else {
            this.setState({
                active: false
            });
        };
    };

    render() {
        return (
            <a className={styles.e} href={this.props.href} title={this.props.title} active={this.state.active.toString()}>
                <h1>
                    {this.props.children}
                </h1>
            </a>
        );
    };
};

class Header extends React.Component {
    render() {
        return (
            <div className={styles.Header}>
                <HeaderInfo />
                <HeaderContent>
                    <HeaderContentItem href="/site/" page="/site/" title="Главная страница">
                        Главная
                    </HeaderContentItem>
                    <HeaderContentItem href="/site/commands/" page="/site/commands/" title="Команды бота">
                        Команды
                    </HeaderContentItem>
                    <HeaderContentItem href="/site/developers/" page="/site/developers/" title="Разработчики">
                        Devs
                    </HeaderContentItem>
                </HeaderContent>
            </div>
        );
    };
};

export default Header;